.sample-container {
  button {
    background-color: #f1f1f100;
  }
  .top {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    h2 {
      text-align: center;
      line-height: 80px;
    }
  }
  .dropdown {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
  }
  .dropdown-toggle {
    border: none;
    width: 100%;
    padding: 5px 8px;
    font-size: 1rem;
  }
  .dropdown-menu {
    &.on {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    display: none;
    position: absolute;
    top: 100%;
    left: 10px;
    width: 200px;
    transition: display 1s;
    margin: 0;
    padding: 0;
    background-color: #f4f4f4;
    border-radius: 10px;
    box-shadow: rgba(15, 15, 15, 0.05) 0px 0px 0px 1px,
      rgba(15, 15, 15, 0.1) 0px 3px 6px, rgba(15, 15, 15, 0.2) 0px 9px 24px;
    li {
      display: flex;
      align-items: center;
      list-style: none;
      width: 100%;
      padding: 5px;
      margin-left: 20px;
      border-bottom: 0.8px solid rgb(210, 210, 210);
      font-size: 16px;
      &:last-child {
        border: none;
      }
    }
    h5 {
      margin: 4px 0;
      font-weight: initial;
      font-size: inherit;
      text-align: center;
    }
    .btn-group {
      display: flex;
      flex-direction: row;
    }
    .btn {
      background-color: inherit;
      border: none;
      color: #3b3b3b;
      font-size: inherit;
      &.on {
        color: rgb(6, 123, 212);
        &:hover {
          font-weight: bolder;
        }
      }
      &:hover {
        font-weight: bold;
      }
    }
  }
  .other {
    background-color: rgb(239, 239, 239);
    text-align: "center";
    justify-content: center;
    align-items: center;
    div {
      font-size: 24px;
      color: #a3a3a3;
    }
  }
}
